<template>
    <div>
        <v-dialog
            max-width="400"
            persistent
            v-model="productTookDialog"
        >
            <template>
                <v-card class="ip_add">

                    <div class="ip_add__title">
                        <span class="text-black">Mahsulot olindi</span>
                        <v-btn icon @click="closeOrderDialog">
                            <img src="@/assets/images/BackButton.svg" alt="BackButton">
                        </v-btn>
                    </div>

                    <ValidationObserver ref="formProductTook" v-slot="{ handleSubmit, invalid }">
                        <form class="ip_add__content" @submit.prevent="handleSubmit(onSubmit)">

                            <div class="mt-3">
                                <span class="text-black ml-1">Sana</span>
                                <v-menu
                                    v-model="dateMenu"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="auto"
                                >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider rules="required" v-slot="{ errors }">
                                            <v-text-field
                                                class="mt-2"
                                                placeholder="Tanlang"
                                                v-model="dateFormatted"
                                                readonly
                                                outlined
                                                v-bind="attrs"
                                                v-on="on"
                                                @blur="date = parseDate(dateFormatted)"
                                                :error-messages="errors"
                                            ></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <ValidationProvider rules="required" v-slot="{ errors }">
                                    <v-date-picker
                                        color="blue lighten-1"
                                        v-model="date"
                                        @input="dateMenu = false"
                                        locale="ru-ru"
                                        first-day-of-week="1"
                                        :error-messages="errors"
                                    ></v-date-picker>
                                    </ValidationProvider>
                                </v-menu>
                            </div>


                            <div>
                                <span class="text-black ml-1">Nechta olindi</span>
                                <ValidationProvider :rules="{required: true,
                                    error: [errorStatus, errorMessage, errorPlace, 'amount']}" 
                                    v-slot="{ errors }">

                                    <v-text-field :error-messages="errors"
                                        @input="formatPrice(product.amount, 'amount')"
                                        v-model="product.amount" min="1" 
                                        outlined placeholder="Misol: 150" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <!-- <div>
                                <span class="text-black ml-1">Dona narxi</span>
                                <ValidationProvider :rules="{required: true,
                                    error: [errorStatus, errorMessage, errorPlace, 'piece_price']}" 
                                    v-slot="{ errors }">

                                    <v-text-field :error-messages="errors" min="1"
                                        v-model="product.piece_price" @keypress="formatPrice()"
                                        outlined placeholder="Misol: 6,000 som" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div> -->


                            <div>
                                <span class="text-black ml-1">Umumiy narx</span>
                                <ValidationProvider :rules="{required: true, 
                                error: [errorStatus, errorMessage, errorPlace, 'total_price']}"
                                 v-slot="{ errors }">
                                    <v-text-field :error-messages="errors"
                                        :value="getTotalPrice" v-model="product.total_price" min="1"
                                        outlined placeholder="Misol: 2,200,000 som" class="mt-2">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>
                            
                            <div>
                                <span class="text-black ml-1">Summa berildi</span>
                                <ValidationProvider v-slot="{ errors }"
                                :rules="{error: [errorStatus, errorMessage, errorPlace, 'paid']}">
                                    <v-text-field :error-messages="errors"
                                        outlined min="1"
                                        @input="formatPrice(product.paid, 'paid')"
                                        placeholder="Misol: 2,000,000 som"
                                        class="mt-2" v-model="product.paid">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>


                            <div class="border-top mb-4"></div>

                            <div>
                                <span class="text-black ml-1">Dona narxi(sotuv uchun)</span>
                                <ValidationProvider v-slot="{ errors }"
                                :rules="{error: [errorStatus, errorMessage, errorPlace, 'price']}">
                                    <v-text-field :error-messages="errors"
                                        outlined min="1"
                                        @input="formatPrice(product.price, 'price')"
                                        placeholder="Misol: 2,000,000 som"
                                        class="mt-2" v-model="product.price">
                                    </v-text-field>
                                </ValidationProvider>
                            </div>



                            <!-- <div>
                                <span class="text-black ml-1">Qarzdorlik</span>
                                <ValidationProvider  v-slot="{ errors }"
                                :rules="{error: [errorStatus, errorMessage, errorPlace, 'debt']}">
                                    <v-text-field :error-messages="errors"
                                        :value="getDebts" outlined min="1"
                                        placeholder="Misol: 1,000,000 som"
                                        class="mt-2" v-model="product.debt">
                                    </v-text-field>
                                </ValidationProvider>
                            </div> -->

                            <!-- 
                            <div>
                                <span class="text-black ml-1">Tannarxi</span>
                                <ValidationProvider  v-slot="{ errors }"
                                :rules="{error: [errorStatus, errorMessage, errorPlace, 'price']}">
                                    <v-text-field :error-messages="errors"
                                        outlined min="1"
                                        placeholder="Misol: 5,000 som"
                                        class="mt-2" v-model="product.price">
                                    </v-text-field>
                                </ValidationProvider>
                            </div> -->

                            <v-card-actions class="d-flex justify-space-between">
                                <v-btn
                                    class="backBtn btn"
                                    elevation="0"
                                    @click="closeOrderDialog"
                                >Bekor qilish
                                </v-btn>
                                <v-btn
                                    class="primaryBtn btn"
                                    elevation="0"
                                    :loading="loading"
                                    :disabled="invalid"
                                    type="submit"
                                    @click="loader = 'loading'"
                                >Tasdiqlash
                                </v-btn>
                            </v-card-actions>

                        </form>
                    </ValidationObserver>
                </v-card>
            </template>
        </v-dialog>
    </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { PerfectScrollbar } from 'vue2-perfect-scrollbar'
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    props: ['productTookDialog', 'item'],
    mixins: [dateMixin, loaderMixin],
    components: {
        PerfectScrollbar
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            product: {
                amount: 0,
                paid: 0,
                price: 0,
                total_price: '',
                // piece_price: '',
                // debt: '',
            },
            
            // ============= SECONDARY_DATA =============




            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============
        }
    },
    watch: {
        // ============= OTHER_WATCHERS START =============
        'product': {
            async handler(newVal){
                if(newVal){
                    this.clearErrors()
                }   
            }, deep: true,
        },

        // ============= OTHER_WATCHERS END =============
    },
    async mounted(){
        await this.refresh()
    },
    computed: {
        ...mapState(
            {

            }
        ),
        // ============= CALCULATE_TOTAL_PRICE START =============
        getTotalPrice() {
            let price = this.item.price.toString().replace(/,/g, '');
            let amount = this.product.amount.toString().replace(/,/g, '');
            price = parseFloat(price)
            amount = parseFloat(amount)

            let result = price * amount
            return  this.product.total_price = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(result);
        }
        // ============= CALCULATE_TOTAL_PRICE END =============
        
        
    },
    methods:{
        ...mapActions(
            {
                postGetReadyProduct: 'factory/postGetReadyProduct',
            }
        ),
        // ============= PRICE_FORMATTER START =============
        formatPrice(value, input) {
            if(value != ''){

                let currency = value.toString().replace(/,/g, '');
                currency = parseFloat(currency)
                currency = new Intl.NumberFormat('en-US', {minimumFractionDigits: 0, maximumFractionDigits: 2}).format(currency);


                if(input == 'amount'){
                    this.product.amount = currency
                }else if(input == 'paid'){
                    this.product.paid = currency
                }else if(input == 'price'){
                    this.product.price = currency
                }
                // else if(input == 'total_price'){
                //     this.product.total_price = currency
                // }
            }
        },
        // ============= PRICE_FORMATTER END =============

        

       
        







        // ============= REFRESH START =============
        async refresh(){
            
        },
        // ============= REFRESH START =============








        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.product = {
                amount: 0, 
                paid: 0, 
                price: 0,
                total_price: '', 
                // piece_price: '', 
                // debt: '', 
            }

            this.date = ''
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeOrderDialog(){
            this.clearInputs();
            this.$parent.productTookDialog = false;
            this.$refs.formProductTook.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============
        
        
        
        
        
        
        
        
   

        // ============= ON_SUBMIT START =============
        async onSubmit(){
            this.$refs.formProductTook.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    // ============= ADDING_PRODUCT_TOOK START =============
                    let product = await this.postGetReadyProduct(
                    {
                        created_at: this.date,
                        amount: parseFloat(this.product.amount.toString().replace(/,/g, '')),
                        paid: parseFloat(this.product.paid.toString().replace(/,/g, '')),
                        order_id: parseInt(this.item.id.toString().replace(/,/g, '')),
                        price: parseFloat(this.product.price.toString().replace(/,/g, '')),
                        // total_price: parseFloat(this.product.total_price),
                        // piece_price: parseFloat(this.product.piece_price),
                        // debt: parseFloat(this.product.debt),
                    })

                    
                    if(product.response && product.response.status == 422){
                        if(product.response.data.errors.paid){
                            this.errorMessage = product.response.data.errors.paid[0];
                            this.errorPlace = 'paid'
                        }else if(product.response.data.errors.amount){
                            this.errorMessage = product.response.data.errors.amount[0];
                            this.errorPlace = 'amount'
                        }

                        this.errorStatus = true
                        this.$toast.error(this.errorMessage);
                    }else if(product.response && product.response.status == 409){
                        if(product.response.data.message){
                            this.errorMessage = product.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        this.$toast.success('Muvaffaqiyatli bajarildi!');
                        this.closeOrderDialog();
                        this.$store.commit('main/SET_UPDATE_FACTORY_CURRENT_PROCESS', true)
                        this.$store.commit('main/SET_UPDATE_READY_PRODUCTS_INCOME', true)
                    }
                    // ============= ADDING_PRODUCT_TOOK END =============


                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })

        }
        // ============= ON_SUBMIT END =============



    }

}
</script>


<style>

</style>
