<template>
    <div>
        <v-data-table
            :headers="headers"
            :items="items"
            item-key="id"
            :loading="loading"
            disable-sort
            hide-default-footer
        >
            <!-- eslint-disable  -->
            <template v-slot:item.name="{item}">
                <div class="d-flex">
                    <span class="text-black">{{ item.name }}</span>
                </div>
            </template>

            <template v-slot:item.code="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">#{{ item.code }}</span>
                </div>
            </template>

            <template v-slot:item.amount="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount) }} ta</span>
                </div>
            </template>

            <template v-slot:item.resource="{item, index}">
                <v-menu 
                    v-model="menu[index]"
                    :close-on-content-click="false"
                    :nudge-width="220"
                    offset-x
                    offset-y>
                    <template v-slot:activator="{ on, attrs }">
                        <a class="d-flex justify-center" v-bind="attrs" v-on="on">
                            <img src="@/assets/icons/eye.svg" alt="eye" class="eye_svg">
                            <span style="color: #2782F6;">Ko`rish</span>
                        </a>
                    </template>
                    <v-list class="process">

                        <div class="process-title">
                            <span>Berilgan resurslar</span>
                            <!-- <div class="d-flex aling-center cursor-pointer" @click="menu[index] = false">
                                <img src="@/assets/images/BackButton.svg" style="width: 30px;">
                            </div> -->
                        </div>


                        <div class="process-content">

                            <div class="process-subtitle">
                                <span>Mato</span>
                            </div>

                            <div class="process-subcontent">
                                <div v-for="(item1, index1) in filteredMato[item.num]" :key="index1">
                                    <span>{{item1.product.name}} - #{{item1.product.code}}</span>
                                    <span>{{item1.amount}} ta</span>
                                </div>
                            </div>

                            <div class="process-subtitle">
                                <span>Yoqa</span>
                            </div>

                            <div class="process-subcontent">
                                <div v-for="(item2, index2) in filteredYoqa[item.num]" :key="index2">
                                    <span>{{item2.product.name}} - #{{item2.product.code}}</span>
                                    <span>{{item2.amount}} ta</span>
                                </div>
                            </div>

                            <div class="process-subtitle">
                                <span>Aksessuar</span>
                            </div>

                            <div class="process-subcontent">
                                <div v-for="(item3, index3) in filteredAksessuar[item.num]" :key="index3">
                                    <span>{{item3.product.name}} - #{{item3.product.code}}</span>
                                    <span>{{item3.amount}} ta</span>
                                </div>
                            </div>

                        </div>
                    </v-list>
                </v-menu>
            </template>

            <template v-slot:item.amount_get="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.amount_get) }} ({{ (item.amount_get_percentage).toFixed(2)}} %)</span>
                </div>
            </template>

            <template v-slot:item.total_price="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ formatPrice(item.total_price) }} so`m</span>
                </div>
            </template>

            <template v-slot:item.date="{item}">
                <div class="d-flex justify-center">
                    <span class="text-black">{{ item.date }}</span>
                </div>
            </template>

            <!-- <template v-slot:item.actions="{item}">
                <div class="d-flex align-center justify-center">
                    <div class="cursor-pointer mr-2 mt-1" v-if="$can('update', 'factory_buyurtmalar')">
                        <img src="@/assets/icons/edit.svg" alt="edit" class="cursor-pointer">
                    </div>
                    <div class="cursor-pointer mt-1" v-if="$can('delete', 'factory_buyurtmalar')">
                        <img src="@/assets/icons/trush-square.svg" alt="edit-2">
                    </div>
                </div>
            </template> -->

            <template v-slot:no-data>
                <div>
                    <no-data class="mt-6"></no-data>
                    <br>
                    <h2 class="ml-6 mt-1">Ma'lumotlar topilmadi!</h2>
                </div>
            </template>
    
        </v-data-table>


        <v-pagination
            v-model="page"
            :length="pageCount"
            total-visible="7"
            class="my-2"
        ></v-pagination>


        <!-- <v-dialog
            max-width="400"
            persistent
            v-model="editModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">Mahsulot tahriri</span>
                    <v-btn icon @click="closeEditModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                
                <ValidationObserver ref="formEditIncomeThread" v-slot="{ handleSubmit, invalid }">
                    <form class="aksessuar_add__content" @submit.prevent="handleSubmit(onSubmit)">
                      
                            
                        <v-card-actions class="d-flex justify-space-between">
                            <v-btn
                                text
                                class="backBtn"
                                elevation="0"
                                @click="closeEditModal"
                            >Bekor qilish
                            </v-btn>
                            <v-btn
                                class="primaryBtn"
                                elevation="0"
                                :loading="loading"
                                :disabled="invalid"
                                type="submit"
                                @click="loader = 'loading'"
                            >Tahrirlash
                            </v-btn>
                        </v-card-actions>

                    </form>
                </ValidationObserver>
            </v-card>
        </v-dialog>



        <DeleteModal :deleteModal="deleteModal"></DeleteModal> -->
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
// import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
// import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],
    components: {
        // DeleteModal,
        NoData
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            menu: [],
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Nomi', value: 'name', sortable: false, align: 'start', class: 'table-header'},
                {text: 'Kodi', value: 'code', sortable: false, align: 'center', class: 'table-header'},
                {text: 'Soni', value: 'amount', sortable: false, align: 'center', class: 'table-header'},
                {text: 'Berilgan resurs', value: 'resource', sortable: false, align: 'center', class: 'table-header'},
                {text: 'Olingan mahsulot (%)', value: 'amount_get', sortable: false, align: 'center', class: 'table-header'},
                {text: 'Summasi', value: 'total_price', sortable: false, align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', sortable: false, align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
            item: {},

            filteredMato: [],
            filteredYoqa: [],
            filteredAksessuar: []
        }
    },
    async mounted(){
        await this.refresh()
    },
    watch:{
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$route.params.id': {
            handler: async function(value) {
                await this.refresh();
            },
            deep: true,
            immediate: true
        },
        '$store.state.main.factory_closed_orders': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_FACTORY_CLOSED_ORDERS', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    computed: {
        ...mapState({
            factory_sewing_history: state => state.factory.factory_sewing_history,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        })
    },
    methods: {
        ...mapActions({
            fetchFactorySewingHistory: 'factory/fetchFactorySewingHistory'
        }),
        async refresh(){
            this.loading = true
            await this.fetchFactorySewingHistory({page: this.page, factory_id: this.$route.params.id, 
            status: 'finished', search: this.searchValue, date: this.dateFilterValue})
            this.page = this.factory_sewing_history.current_page;
            this.pageCount = Math.ceil(this.factory_sewing_history.total / 10);
            this.items = this.factory_sewing_history.data;
            this.items.forEach((elem, index) => {
                elem.num = index;
                elem.name = elem.product.name;
                elem.code = elem.product.code;
                elem.amount_get = 0;
                elem.order_incomes.forEach((elem1) => {
                    elem.amount_get = elem.amount_get + elem1.amount
                })
                elem.amount_get_percentage = (elem.amount_get * 100) / elem.amount
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
            
            this.factory_sewing_history.data.forEach((item, index) =>{
                this.filteredMato[index] = item.order_extra_product.filter((elem) => {
                    return elem.product.product_category.parent_id == 2
                })
                console.log(this.filteredMato);
                this.filteredYoqa[index] = item.order_extra_product.filter((elem) => {
                    return elem.product.product_category.parent_id == 3
                })
                this.filteredAksessuar[index] = item.order_extra_product.filter((elem) => {
                    return elem.product.product_category.parent_id == 4
                })
            })
        },
        closeEditModal(){
            this.editModal = false
        }
    }
};
</script>

