<template>
<div>
    <v-dialog
        max-width="400"
        persistent
        v-model="closeProductDialog"
    >
        <v-card class="aksessuar_add">

            <div class="aksessuar_add__title">
                <span class="text-black">Mahsulot yopilishi</span>
                <v-btn icon @click="closeModal()">
                    <img src="@/assets/images/BackButton.svg" alt="BackButton">
                </v-btn>
            </div>

            <div class="pb-5 pt-4 mb-3 text-muted border-bottom">
                Bu mahsulotni yopmoqchimisiz?
            </div>

            <v-card-actions class="d-flex justify-space-between">
                <v-btn
                    text
                    class="backBtn"
                    elevation="0"
                    @click="closeModal()"
                >Yoq
                </v-btn>
                <v-btn
                    class="primaryBtn"
                    elevation="0"
                    @click="removeProduct"
                    :loading="loading"
                >Ha
                </v-btn>
            </v-card-actions>

        </v-card>
    </v-dialog>
</div>
</template>

<script>
import {mapState, mapActions} from "vuex";
import { dateMixin, loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    mixins: [dateMixin, loaderMixin],
    props: ['item', 'closeProductDialog'],
    data() {
        return {

        }
    },
    computed: {
        ...mapState(
        {

        }),
    },
    methods: {
        ...mapActions(
        {
            postFinishedOrder: 'factory/postFinishedOrder'
        }),
        closeModal(){
            this.$parent.closeProductDialog = false;
        },
        async removeProduct() {
            await this.postFinishedOrder({order_id: this.item.id})
            this.closeModal()
            let toastTitle = 'Muvaffaqiyatli yopildi!'
            this.$toast.success(toastTitle);
            this.$store.commit('main/SET_UPDATE_FACTORY_CURRENT_PROCESS', true)
            this.$store.commit('main/SET_UPDATE_FACTORY_CLOSED_ORDERS', true)
        }
    },
   
}
</script>

<style scoped>

